export default class InsertImage {
    constructor(imgEl, imgSrc) {
        this.imgEl = imgEl;
        this.imgSrc = imgSrc;
    }
    insertImage() {
        const img = new Image();
        img.src = this.imgSrc;
        this.imgEl.appendChild(img);
    }
}


